import {Component, OnInit, trigger, state, style, transition, animate} from "@angular/core";
import {ActivatedRoute} from "@angular/router";
import {AuthService} from "../../../services/auth.service";
import {AppState} from "../../../../../app.service";

@Component({
    selector: 'app-activate-account',
    template: require('./activate-account.component.html'),
    animations: [
        trigger('routeAnimation', [
            state('*', style({transform: 'scale(1)', opacity: 1})),
            transition('void => *', [
                style({transform: 'scale(0)', opacity: 0}),
                animate(1000)
            ]),
            transition('* => void', animate(1000, style({transform: 'scale(0)', opacity: 0})))
        ])
    ],
})
export class ActivateAccountComponent implements OnInit {
    public success: string;
    public errors: any;
    public constructor(
        public router: ActivatedRoute,
        public authService: AuthService,
        public appState: AppState
    ) {}

    public ngOnInit() {
        this.router.params.subscribe(params => {
            this.authService.activateUserAccount({active_code: params['code']})
                .subscribe(
                    response => {
                        this.success = response.message;
                        // if the user not logged in log him in
                        localStorage.setItem('token', response.token);
                        this.appState.set('authorized', true);
                        this.appState.set('user', response.user);
                    },
                    error => {
                        this.errors = error.json();
                    }
                );
        });
    }


}



