import {Component, OnInit, trigger, state, style, transition, animate} from "@angular/core";
import {Router} from "@angular/router";
import {AppState} from "../../../../../app.service";
import {AuthService} from "../../../services/auth.service";

@Component({
    selector: 'app-logout',
    template: require('./logout.component.html'),
    animations: [
        trigger('routeAnimation', [
            state('*', style({transform: 'scale(1)', opacity: 1})),
            transition('void => *', [
                style({transform: 'scale(0)', opacity: 0}),
                animate(1000)
            ]),
            transition('* => void', animate(1000, style({transform: 'scale(0)', opacity: 0})))
        ])
    ],
})
export class LogoutComponent implements OnInit {

    public constructor(
        public appState: AppState,
        public router: Router,
        public authService: AuthService
    ) {}

    public ngOnInit() {
        this.authService.logout()
            .subscribe(() => this.logout(), () => this.logout());
    }

    private logout() {
        setTimeout(() => {
            // Destroy token
            localStorage.removeItem('token');
            // set authorized in app state to false
            this.appState.set('authorized', false);
            this.appState.set('user', {});
            this.router.navigate(['auth', 'login']);
        }, 1500);
    }
}



