import {Component, OnInit, trigger, state, style, transition, animate} from "@angular/core";
import {ActivatedRoute, Router} from "@angular/router";
import {UserInterface, FormInterface} from "../../../contracts";
import {GuestService} from "../../../services/guest.service";
import {AppState} from "../../../../../app.service";

@Component({
    selector: 'app-reset-email',
    template: require('./reset-email.component.html'),
    animations: [
        trigger('routeAnimation', [
            state('*', style({transform: 'scale(1)', opacity: 1})),
            transition('void => *', [
                style({transform: 'scale(0)', opacity: 0}),
                animate(1000)
            ]),
            transition('* => void', animate(1000, style({transform: 'scale(0)', opacity: 0})))
        ])
    ],
})
export class ResetEmailComponent implements OnInit {
    public user: UserInterface = <UserInterface>{};
    public form: FormInterface = <FormInterface>{};

    public constructor(
        public router: ActivatedRoute,
        public guestService: GuestService,
        public appState: AppState,
        public _router: Router
    ) {}

    public ngOnInit() {
        this.router.params.subscribe(params => {
            this.user.token = params['code'];
        });
    }

    public reset() {
        this.form.submitting = true;

        this.guestService.resetPassword(this.user)
            .subscribe(
                response => {
                    localStorage.setItem('token', response.token);
                    this.appState.set('authorized', true);
                    this.appState.set('user', response.user);
                    this._router.navigate(['']);
                }, errors => {
                    this.form.errors = errors.json();
                    this.form.submitting = false;
                }
            );
    }

}



