import {Component, trigger, state, style, transition, animate} from "@angular/core";
import {Router} from "@angular/router";
import {AppState} from "../../../../../app.service";
import {FormInterface, UserInterface} from "../../../contracts";
import {GuestService} from "../../../services/guest.service";

@Component({
    selector: 'app-login',
    template: require('./login.component.html'),
    styles: [`
        @media (max-width: 450px) {
            .pull-xs-none {
                display: block;
            }
        }
    `],
    animations: [
        trigger('routeAnimation', [
            state('*', style({opacity: 1})),
            transition('void => *', [
                style({opacity: 0}),
                animate(1000)
            ]),
            transition('* => void', animate(1000, style({opacity: 0})))
        ])
    ],
})
export class LoginComponent {
    public user: UserInterface = <UserInterface>{};
    public form : FormInterface = <FormInterface>{};

    public constructor(
        public guestService: GuestService,
        private appState: AppState,
        public router: Router
    ) {}

    public login() {
        this.form.submitting = true;

        this.guestService.login(this.user)
            .subscribe(
                response => {
                    localStorage.setItem('token', response.token);
                    this.appState.set('authorized', true);
                    this.appState.set('user', response.user);
                    this.router.navigate(['']);

                }, error => {
                    this.form.errors = error.json();
                    this.form.submitting = false;
                }
            );
    }

}



