import {NgModule} from "@angular/core";
import {RouterModule} from "@angular/router";
import {CommonModule} from "@angular/common";
import {FormsModule} from "@angular/forms";
import {HttpModule} from "@angular/http";
import {GuestService, AuthService} from "../../services";
import {LoginComponent} from "./login/login.component";
import {LogoutComponent} from "./logout/logout.component";
import {RegisterComponent} from "./register/register.component";
import {ResetEmailComponent} from "./reset-email/reset-email.component";
import {ForgotPasswordComponent} from "./forgot-password/forgot-password.component";
import {MainAuthLayoutComponent} from "./main-auth-layout/main-auth-layout.component";
import { ActivateAccountComponent } from './activate-account/activate-account.component';

const AUTH_ROUTES = [
    {
        path: '',
        component: MainAuthLayoutComponent,
        children: [
            {path: 'register', component: RegisterComponent, pathMatch: 'full', canActivate: [GuestService]},
            {path: 'login', component: LoginComponent, pathMatch: 'full', canActivate: [GuestService]},
            {path: 'logout', component: LogoutComponent, pathMatch: 'full', canActivate: [AuthService]},
            {path: 'forgot-password', component: ForgotPasswordComponent, pathMatch: 'full', canActivate: [GuestService]},
            {path: 'password/reset/:code', component: ResetEmailComponent, pathMatch: 'full', canActivate: [GuestService]},
            {path: 'account/activate/:code', component: ActivateAccountComponent, pathMatch: 'full'},
        ]
    },

];


@NgModule({
    declarations: [
        RegisterComponent,
        LoginComponent,
        ForgotPasswordComponent,
        ResetEmailComponent,
        LogoutComponent,
        MainAuthLayoutComponent,
        ActivateAccountComponent
    ],
    imports: [
        CommonModule,
        FormsModule,
        HttpModule,
        RouterModule.forChild(AUTH_ROUTES)
    ],
    providers: [

    ]
})

export class AuthModule {}



