import {Component, trigger, state, style, transition, animate} from "@angular/core";
import {GuestService} from "../../../services/guest.service";
import {FormInterface, UserInterface} from "../../../contracts";

@Component({
    selector: 'app-forgot-password',
    template: require('./forgot-password.component.html'),

    animations: [
        trigger('routeAnimation', [
            state('*', style({transform: 'scale(1)', opacity: 1})),
            transition('void => *', [
                style({transform: 'scale(0)', opacity: 0}),
                animate(1000)
            ]),
            transition('* => void', animate(1000, style({transform: 'scale(0)', opacity: 0})))
        ])
    ],
})
export class ForgotPasswordComponent {
    public user: UserInterface = <UserInterface>{};
    public form: FormInterface = <FormInterface>{};

    public constructor(public guestService: GuestService) {
        this.form.message = 'Enter your <b>Email</b>  and instructions will be sent to you!';
    }

    public reset() {
        this.form.submitting = true;

        this.guestService.forgotPassword(this.user)
            .subscribe(
                response => {
                    this.form.submitted = true;
                    this.form.errors = {};
                    this.form.message = response;
                }, error => {
                    this.form.message = '';
                    this.form.errors = error.json();
                    this.form.submitting = false;
                    this.form.submitted = false;
                }
            );
    }
}



